type MenuItem = {
        name: string;
        price: number;
        desc: string;
        cals?: number;
        tags?: string[];
      };
      
      export default function MenuCard({ item }: { item: MenuItem }) {
        return (
          <article className="rounded-2xl border bg-white p-5 shadow-soft">
            <div className="flex items-start justify-between gap-3">
              <h3 className="font-semibold text-lg">{item.name}</h3>
              <div className="font-semibold text-tufts-blue">${item.price.toFixed(2)}</div>
            </div>
            <p className="mt-1 text-sm text-neutral-700">{item.desc}</p>
            <div className="mt-3 flex flex-wrap gap-2">
              {item.cals ? (
                <span className="text-xs px-2 py-1 rounded-full bg-neutral-100 border">
                  {item.cals} kcal
                </span>
              ) : null}
              {item.tags?.map((t) => (
                <span key={t} className="text-xs px-2 py-1 rounded-full bg-neutral-100 border">
                  {t}
                </span>
              ))}
            </div>
          </article>
        );
      }
      